@import '../assets/variables.scss';
@import '../assets/mixins.scss';


.header-wrapper {
    width: 100%;
    @include height(100);
    display: flex;
    align-items: center;
    background-color: $background;
    @include padding-upside(0, 25);
    border-bottom: 2px solid $greyBlue95;

    .header-menu-icon {
        @include icon(50%, '../images/header-menu.png');
        @include margin-r(35);
    }
    .header-title {
        @include font-size(18);
        color: $greyBlue50;
    }
    .navigation {
        @include margin-upside(0, 78);
        display: flex;
        align-items: center;

        .menu-link {
            @include font-size(13);
            color: $greyBlue70;
            @include margin-r(25);
            font-weight: 600;
        }
        .more-menu {
            background: url(../images/more.png) center / 60% no-repeat;
            @include w-h(30, 30);
        }
    }
    
    
    .form-wrapper {
        position: relative;
        @include margin-r(80);

        .search {
            @include w-h(425, 50);
            border-radius: 30px;
            background-color: white;
            @include padding-l(60);
            padding-bottom: 1%;
        }
        .search::placeholder {
            color: $greyBlue80;
            @include font-size(12);
        }
        .search-btn {
            @include absolute-l(16, 26);
            @include w-h(17, 17);
            background: url('../images/search.png') center / 90% no-repeat;
        }
        .search-arrow {
            @include absolute-r(19, 26);
            @include w-h(7, 11);
            background: url('../images/search-arrow.png') center / 90% no-repeat;
        }
    }
    .user-name {
        @include font-size(13);
        color: $greyBlue60;
        @include margin(0, 32, 0, 48);
        position: relative;
    }
    .user-name::before {
        content: '';
        @include w-h(20, 20);
        @include absolute-l(0, -32);
        background: url('../images/user-icon.png') center / 95% no-repeat;
        z-index: 1;
    }
    .alerts {
        @include icon(35%, '../images/alerts.png');
        @include margin-r(10);
        position: relative;
    }
    .alerts::after {
        content: '';
        @include w-h(10, 10);
        background-color: $pink;
        border-radius: 30px;
        @include absolute-r(0, 0);
    }
    .close {
        @include icon(40%, '../images/closing.png');
    }
    .mobile-user {
        @include icon(40%, '../images/user-icon.png');
    }
}
@media (max-width: 1100px) {
    .header-wrapper {

        .navigation {
            .menu-link {
                font-size: 0.7rem;
            }
        }
    }
}

@media (max-width:600px) {
    .header-wrapper {
        height: 100px;
        padding: 25px; 
    
    

        .header-menu-icon, .mobile-user {
            @include width-height(50px, 50px);

        }
        .header-menu-icon {
            background-size: 35%;
        }
        .mobile-user {
            margin-left: auto;
        }
        .header-title {
            font-size: 18px;
            padding-left: 27px;
            font-weight: 600;
        }
    }
}